import axios from 'axios';
import authService from './auth.service';

const API_BASE_URL = 'http://localhost:5001/api';
const USE_MOCK_DATA = process.env.NODE_ENV === 'development' && process.env.REACT_APP_USE_MOCK_DATA === 'true';

// Types for AI Note Checker
export interface EligibleEncounter {
  encounterId: string;
  patientId: string;
  patientName: string;
  chiefComplaint: string;
  dateOfService: string;
  status: string;
}

export interface AIAnalysisIssue {
  assessment: string;
  issue: 'no_explicit_plan' | 'chronicity_mismatch' | 'unclear_documentation' | 'chief_complaint_structure';
  details: {
    HPI?: string;
    'A&P': string;
    correction: string;
  };
}

export interface AIAnalysisResult {
  issues?: AIAnalysisIssue[];
  status: 'ok' | 'corrections_needed';
  summary?: string;
}

export interface NoteCheckResult {
  id: number;
  encounterId: string;
  patientId: string;
  patientName: string;
  chiefComplaint: string;
  dateOfService: string;
  status: 'pending' | 'completed' | 'error';
  aiAnalysis?: AIAnalysisResult;
  issuesFound: boolean;
  checkedAt: string;
  checkedBy: string;
  errorMessage?: string;
}

export interface BatchProcessResult {
  processed: number;
  successful: number;
  failed: number;
  results: NoteCheckResult[];
}

export interface CareTeamMember {
  id: string;
  active: boolean;
  providerId: string;
  encounterId: string;
  encounterRoleType: 'PROVIDER' | 'SECONDARY_PROVIDER' | 'STAFF' | 'COSIGNING_PROVIDER';
  firstName: string;
  lastName: string;
  title?: string;
  onlineCheckInEnabled: boolean;
  fullOnlineCheckInEnabled: boolean;
}

export interface CreatedToDo {
  id: number;
  encounterId: string;
  patientId: string;
  patientName: string;
  ezDermToDoId: string;
  subject: string;
  description: string;
  assignedTo: string;
  assignedToName: string;
  ccList: any[];
  issuesCount: number;
  createdBy: string;
  createdAt: Date;
}

export interface InvalidIssue {
  id: number;
  encounterId: string;
  checkId: number;
  issueIndex: number;
  issueType: string;
  assessment: string;
  issueHash: string;
  markedInvalidBy: string;
  markedInvalidAt: Date;
  reason?: string;
}

export interface ProgressNoteResponse {
  progressNotes: Array<{
    sectionType: 'SUBJECTIVE' | 'OBJECTIVE' | 'ASSESSMENT_AND_PLAN';
    locked: boolean;
    order: number;
    items: Array<{
      elementType: string;
      text: string;
      autogenerated: boolean;
      maxCodingValue: number;
      suggestedCodingValue: number;
      order: number;
      note?: string;
      multipleItemValue: any[];
    }>;
  }>;
  groupCodes: {
    problemPointsV2: number;
    dataPointsV2: number;
    riskPointsV2: number;
    emCode: string;
  };
  encounterNonBillableStatuses: {
    active: boolean;
    encounterId: string;
    billableEncounter: boolean;
    emCoding: boolean;
  };
  patientId: string;
  notesCount: number;
}

